import {isType} from './isType';

/**
 * @name isArray
 * @description    
 * @param {Array} arr - 
 * @return {Boolean}      
 * @api
 * isArray(*)
 * @example
 * isArray([]); // true
 */
const isArray = (arr) => {
    return isType(arr, 'array');
};

export {isArray};



