import {isArray} from './isArray';

/**
 * @name reverse
 * @description   (  )
 * @param {Array} arr - 
 * @param {Function} cb - 
 * @param {*} context - 
 * @return {Array}   
 * @api
 * reverse(object, function)
 * @example
 * reverse(['a', 'b', 'c', 'd'], function(value, index, array) {
 *     alert('value:' + value + ', index:' + index);
 *     if (value === 'b') { return false; } // false    .
 * });
 */
const reverse = (arr, cb, context) => {
    if (!arr) { return arr; }

    if (isArray(arr)) {
        for (let i = arr.length - 1; i >= 0; i--) {
            if (cb.call(context || arr, arr[i], i, arr) === false) { break; }
        }
    } else {
        throw new Error('reverse     .');
    }

    return arr;
};

export {reverse};



