import {Core} from '../Core/core';

/**
 * @namespace
 * @name Core.Env
 * @description     
 */
const addonEnv = () => {
    Core.define('env', {
        configs: {},

        /**
         * @name set
         * @description   
         * @param {string} name . `.`      .
         * @param {*} value 
         * @return {*} 
         * @example
         * {{LIB_NAME}}.Env.set('siteTitle', 'Hello World!');
         */
        set: function (name, value) {
            let root = this.configs,
                names = name.split('.'),
                len = names.length,
                last = len - 1,
                pair = root;

            for (let i = 0; i < last; i++) {
                pair = pair[names[i]] || (pair[names[i]] = {});
            }

            return (pair[names[last]] = value);
        },

        /**
         * @name get
         * @description   
         * @param {string} name . `.`      .
         * @param {*} [def]      
         * @return {*} 
         * @example
         * {{LIB_NAME}}.Env.get('siteTitle'); // 'Hello World!'
         */
        get: function (name, def) {
            let root = this.configs,
                names = name.split('.'),
                pair = root;

            for (let i = 0, len = names.length; i < len; i++) {
                if (!(pair = pair[names[i]])) { return def; }
            }

            return pair;
        }
    });
};

export {addonEnv};



