import {Core} from '../Core/core';

/**
 * @namespace
 * @name Core.string
 * @description     
 */
const addonString = () => {
    Core.define('string', function() {
        const escapeChars = {
            '&': '&amp;',
            '>': '&gt;',
            '<': '&lt;',
            '"': '&quot;',
            "'": '&#39;'
        },
        unescapeChars = (function(escapeChars) {
            let results = {};

            Core.each(escapeChars, function (v, k) {
                results[v] = k;
            });

            return results;
        })(escapeChars),
        escapeRegexp = /[&><'"]/g,
        unescapeRegexp = /\&[^;]+;/g, // /(&amp;|&gt;|&lt;|&quot;|&#39;|&#[0-9]{1,5};)/g,
        tagRegexp = /<\/?[^>]+>/gi,
        scriptRegexp = /<script\b[^<]*(?:(?!<\/script>)<[^<]*)*<\/script>/ig,
        hexRegexp = /^\&#x([\da-fA-F]+);$/;

        return {
            /**
             * @name {{LIB_NAME}}.string.trim
             * @description   
             * @param {String} value - 
             * @return {String}     
             * @example
             * {{LIB_NAME}}.string.trim(' abc '); // 'abc'
             */
            trim: function(value) {
                return (value)? value.replace(/^\s+|\s+$/g, '') : value;
            },

            /**
             * @name {{LIB_NAME}}.string.replaceAll
             * @description      
             * @param {String} value -   
             * @param {RegExp|String} - find    
             * @param {String} rep -  
             * @return {String}   
             * @example
             * {{LIB_NAME}}.string.replaceAll('a,b,c,d', ',', ''); // 'abcd'
             */
            replaceAll: function (value, find, rep) {
                if (find.constructor === RegExp) {
                    return value.replace(new RegExp(find.toString().replace(/^\/|\/$/gi, ''), 'gi'), rep);
                }

                return value.split(find).join(rep);
            },

            /**
             * @name {{LIB_NAME}}.string.byteSize
             * @description    
             * @param {String} value -   
             * @return {Number}   
             * @example
             * {{LIB_NAME}}.string.byteSize(''); // euckr:8byte, utf8:12byte
             */
            byteSize: function(value) {
                if (!value) { return 0; }

                return encodeURIComponent(value).replace(/%[A-F\d]{2}/g, 'U').length;
            },

            /**
             * @name {{LIB_NAME}}.string.getFileName
             * @description  path  
             * @param {String} str - path
             * @return {String}    
             * @example
             * {{LIB_NAME}}.string.getFileName('etc/bin/jslib.js'); // 'jslib.js'
             */
            getFileName: function(str) {
                const paths = str.split(/\/|\\/g);

                return paths[paths.length - 1];
            },

            /**
             * @name {{LIB_NAME}}.string.getFileExt
             * @description  path  
             * @param {String} fname - path
             * @return {String}    
             * @example
             * {{LIB_NAME}}.string.getFileExt('etc/bin/jslib.js'); // 'js'
             */
            getFileExt: function(fname) {
                fname || (fname = '');

                return fname.substr((~-fname.lastIndexOf('.') >>> 0) + 2);
            },

            /**
             * @name {{LIB_NAME}}.string.cut
             * @description      ,   
             * @param {String} value - 
             * @param {Number} length -  
             * @param {String='...'} [truncation = '...'] - 
             * @return {String}   
             * @example
             * {{LIB_NAME}}.string.cut('', 3, '...'); // '...'
             */
            cut: function(value, length, truncation) {
                const str = value;

                truncation || (truncation = '');

                if (str.length > length) { return str.substring(0, length) + truncation; }

                return str;
            },

            /**
             * @name {{LIB_NAME}}.string.cutByByte
             * @description    ()  ,   
             * @param {String} value - 
             * @param {Number} length -  
             * @param {String='...'} [truncation = '...'] - 
             * @return {String}   
             * @example
             * {{LIB_NAME}}.string.cutByByte('', 3, '...'); // '...'
             */
            cutByByte: function(value, length, truncation) {
                const str = value,
                    chars = this.indexByByte(value, length);

                truncation || (truncation = '');

                if (str.length > chars) { return str.substring(0, chars) + truncation; }

                return str;
            },

            /**
             * @name {{LIB_NAME}}.string.indexByByte
             * @description    char index (UTF-8   3 )
             * @param {String} value - 
             * @param {Number} length -  
             * @return {Number}    char index 
             * @example
             * {{LIB_NAME}}.string.indexByByte('', 3); // 2
             */
            indexByByte: function(value, length) {
                let len, i, c;

                if (typeof value !== 'string') { return 0; }

                for (len = i = 0; c = value.charCodeAt(i++);) {
                    len += c>>11 ? 3 : c>>7 ? 2 : 1;

                    if (len > length) { return (i > 0)? i - 1 : 0; }
                }

                return i;
            },

            /**
             * @name {{LIB_NAME}}.string.capitalize
             * @description       
             * @param {String} value - 
             * @return {String}  
             * @example
             * {{LIB_NAME}}.string.capitalize('abCdEfg'); // 'Abcdefg'
             */
            capitalize: function(value) {
                return (value)? value.charAt(0).toUpperCase() + value.substring(1) : value;
            },

            /**
             * @name {{LIB_NAME}}.string.toFirstLower
             * @description   
             * @param {String} value - 
             * @returns {String}  
             * @example
             * {{LIB_NAME}}.string.toFirstLower('Welcome'); // 'welcome'
             */
            toFirstLower: function(value) {
                return (value)? value.replace(/^[A-Z]/, function(s) {
                    return s.toLowerCase();
                }) : value;
            },

            /**
             * @name {{LIB_NAME}}.string.camelize
             * @description   
             * @param {String} value - 
             * @return {String}  
             * @example
             * {{LIB_NAME}}.string.camelize('ab-cd-efg'); // 'abCdEfg'
             */
            camelize: function(value) {
                return (value)? value.replace(/(\-|_|\s)+(.)?/g, function (a, b, c) {
                    return ((c)? c.toUpperCase() : '');
                }) : value
            },

            /**
             * @name {{LIB_NAME}}.string.dasherize
             * @description   
             * @param {String} value - 
             * @return {String}  
             * @example
             * {{LIB_NAME}}.string.dasherize('abCdEfg'); // 'ab-cd-efg'
             */
            dasherize: function(value) {
                return (value)? value.replace(/[_\s]+/g, '-').replace(/([A-Z])/g, '-$1').replace(/-+/g, '-').toLowerCase() : value;
            },

            /**
             * @name {{LIB_NAME}}.string.repeat
             * @description       
             * @param {String} value - 
             * @param {Number} cnt -  
             * @return {String}  
             * @example
             * {{LIB_NAME}}.string.repeat('ab', 4); // 'abababab'
             */
            repeat: function(value, cnt, sep) {
                sep || (sep = '');

                let result = [];

                for (let i = 0; i < cnt; i++) {
                    result.push(value);
                }

                return result.join(sep);
            },

            /**
             * @name {{LIB_NAME}}.string.escapeHTML
             * @description  HTML ENTITY 
             * @param {String} value - 
             * @return {String}  
             * @example
             * {{LIB_NAME}}.string.escapeHTML('<div><a href="#"></a></div>'); // "&lt;div&gt;&lt;a href=&quot;#&quot;&gt;&lt;/a&gt;&lt;/div&gt;"
             */
            escapeHTML: function(value) {
                return (value)? (value + '').replace(escapeRegexp, function(m) {
                    return escapeChars[m];
                }) : value;
            },

            /**
             * @name {{LIB_NAME}}.string.unescapeHTML
             * @description HTML ENTITY     
             * @param {String} value - 
             * @return {String}  
             * @example
             * {{LIB_NAME}}.string.unescapeHTML('&lt;div&gt;&lt;a href=&quot;#&quot;&gt;&lt;/a&gt;&lt;/div&gt;');  // '<div><a href="#"></a></div>'
             */
            unescapeHTML: (function() {
                return function(value) {
                    let temp = doc.createElement('div'),
                        result = '';

                    temp.innerHTML = value;

                    for (let i = -1, item; item = temp.childNodes[++i];) {
                        result += item.nodeValue;
                    }

                    temp = null;

                    return result;
                };
            })(),

            /**
             * @name {{LIB_NAME}}.string.toggle
             * @description value === these  other ,  value !== these  value  
             * @param {String} value -  
             * @param {String} these -  
             * @param {String} other -  
             * @return {String}   
             * @example
             * //  
             * {{LIB_NAME}}.string.toggle('ASC', 'ASC', 'DESC'); // 'DESC'
             * {{LIB_NAME}}.string.toggle('DESC', 'ASC', 'DESC'); // 'ASC'
             */
            toggle: function(value, these, other) {
                return (value === these)? other : value;
            },

            /**
             * @name {{LIB_NAME}}.string.format
             * @description    {}        
             * @param {String} format - 
             * @param {String|Object} val -  
             * @return {String}  
             * @example
             * {{LIB_NAME}}.string.format('{0}:{1}:{2} {0}', 'a', 'b', 'c');  // 'a:b:c a'
             * {{LIB_NAME}}.string.format('{a}:{b}:{c} {d}', {a: 'a', b: 'b', c: 'c', d: 'd'});  // 'a:b:c a'
             */
            format: function(format, val) {
                const args = Core.array.toArray(arguments).slice(1),
                    isJson = Core.is(val, 'object');

                return format.replace(/\{([0-9a-z_]+)\}/ig, function (m, i) {
                    return (isJson)? val[i] : args[i] || '';
                });
            },

            /**
             * @name {{LIB_NAME}}.string.toEntities
             * @description  HTML ENTITIES 
             * @param {String} value - HTML 
             * @return {String} HTML  
             * @example
             * {{LIB_NAME}}.string.toEntities('/'); // '&#47'
             */
            toEntities: function(value) {
                let buffer = [];

                for (let i = 0, len = value.length; i < len; i++) {
                    buffer.push('&#', value.charCodeAt(i).toString(), ';');
                }

                return buffer.join('');
            },

            /**
             * @name {{LIB_NAME}}.string.random
             * @description  
             * @param {Number} len - 
             * @return {String}  
             * @example
             * {{LIB_NAME}}.string.random(10); // '4r1kf9piku'
             */
            random: function(len) {
                let keystr = '',
                    x = 0;

                for (let i = 0; i < len; i++) {
                    x = Math.floor((Math.random() * 36));

                    if (x < 10) {
                        keystr += String(x);
                    } else {
                        keystr += String.fromCharCode(x + 87);
                    }
                }

                return keystr;
            },

            /**
             * @name {{LIB_NAME}}.string.stripTags
             * @description   HTML 
             * @param {string} value - 
             * @return {string}   
             * @example
             * {{LIB_NAME}}.string.stripTags('welcome to <b>the</b> jungle'); // 'welcome to the jungle'
             */
            stripTags: function(value) {
                return (value || '').toString().replace(tagRegexp, '');
            },

            /**
             * @name {{LIB_NAME}}.string.stripScripts
             * @description    
             * @param {string} value - 
             * @return {string}   
             * @example
             * {{LIB_NAME}}.string.stripScripts('welcome <s'+'cript>alert('hello');</s'+'cript> to the jungle'); // 'welcome to the jungle'
             */
            stripScripts: function(value) {
                return (value||'').toString().replace(scriptRegexp, '');
            },

            /**
             * @name {{LIB_NAME}}.string.isKor
             * @description      
             * @param {string} value - 
             * @param {string} type -   ('ONLY' || 'INCLUDE')
             * @return {boolean} 
             * @example
             * {{LIB_NAME}}.string.isKor('1', 'ONLY'); // false
             */
            isKor: function(value, type) {
                let check;

                switch (type) {
                    case 'ONLY':
                        check = /([^---])/i;

                        if (check.test(value)) {
                            return false;
                        } else {
                            return true;
                        }

                        break;
                    case 'INCLUDE':
                        check = /[-|-|-]/;

                        if (check.test(value)) {
                            return true;
                        } else {
                            return false;
                        }

                        break;
                }
            }
        };
    });
};

export {addonString};



