import {isType} from './isType';

/**
 * @name isObject
 * @description    
 * @param {Array} arr - 
 * @return {Boolean}      
 * @api
 * isObject(*)
 * @example
 * isObject(new Object()); // true
 */
const isObject = (obj) => {
    return isType(obj, 'object');
};

export {isObject};



