import {Core} from '../Core/core';

/**
 * @namespace
 * @name Core.number
 * @description     
 */
const addonNumber = () => {
    Core.define('number', {
        /**
         * @name {{LIB_NAME}}.number.addComma
         * @param {Number} value - 
         * @description   ,() 
         * @return {String}      
         * @example
         * {{LIB_NAME}}.number.addComma(21342); // '21,342'
         */
        addComma: (function() {
            const regComma = /(\d+)(\d{3})/;

            return function(value) {
                value += '';

                let x = value.split('.'),
                    x1 = x[0],
                    x2 = (x.length > 1)? '.' + x[1] : '';

                while (regComma.test(x1)) {
                    x1 = x1.replace(regComma, '$1' + ',' + '$2');
                }

                return x1 + x2;
            };
        })(),

        /**
         * @name {{LIB_NAME}}.number.random
         * @param {Number} min - 
         * @description min ~ max  
         * @param {Number} max - 
         * @return {Number}  
         */
        random: function(min, max) {
            if (!max) {
                max = min;
                min = 0;
            }

            return min + Math.floor(Math.random() * (max - min + 1));
        },

        /**
         * @name {{LIB_NAME}}.number.limit
         * @param {Number} value - 
         * @description value min   min, max   max 
         * @param {Number} min - 
         * @param {Number} max - 
         * @return {Number}  
         */
        limit: function(value, min, max) {
            if (value < min) {
                return min;
            } else if (value > max) {
                return max;
            }

            return value;
        },

        /**
         * @name {{LIB_NAME}}.number.parse
         * @param {*} value -  +   
         * @description    (       )
         * @return {Number}  
         * @example
         * {{LIB_NAME}}.number.parse('100'); // 100
         */
        parse: function(value) {
            value = (value || '').toString().replace(/[^-0-9\.]+$/, '');
            value = value * 1;

            return (isNaN(value))? 0 : value;
        },

        /**
         * @name {{LIB_NAME}}.number.toKor
         * @param {Number} num - 
         * @description   
         * @return {String}    
         * @example
         * {{LIB_NAME}}.number.toKor(123456); // 
         */
        toKor: function(num) {
            let nums = [],
                sign = '',
                c = 0,
                c2 = 0,
                result = '',
                ch = '';
            const kor = ['', '', '', '', '', '', '', '', '', ''],
                unit = ['', '', '', ''],
                unit2 = ['', '', '', '', '', ''];

            if (typeof num === null) { return ''; }

            num = num + '';

            if (num === '0') { return ''; }

            if (num.substr(0, 1) === '-') {
                sign = ' ';
                num = num.substr(1);
            }

            nums = num.split('');

            for (let i = nums.length - 1; i >= 0; i--, c++) {
                if (c > 0 && c % 4 === 0) { c2++; }
                if (!(ch = kor[nums[i]])) { continue; }

                if (c % 4 === 0) {
                    result = unit2[c2] + result; // , , , , 

                    if (ch === '' && (i === 0 && c2 <= 1)) { ch = ''; }
                } else {
                    if (ch === '') { ch = ''; }
                }

                if (ch += unit[c % 4]) {
                    result = ch + result;
                }
            }

            return sign + result;
        }
    });

    /**   */
    Core.comma = Core.number.addComma;
};

export {addonNumber};



