/**
 * @name clone
 * @description   
 * @param {Array|Object} obj   json
 * @return {Array|Object}     .
 * @api
 * clone(object)
 * @example
 * var ori = {a: 'A', b: [1, 2, 3]};
 * var clone = clone(ori); // {a: 'A', b: [1, 2, 3]};
 * // ori , ori  clone  .
 */
const clone = (obj) => {
    if ((typeof obj === null) || (typeof obj !== 'object')) { return obj; }

    if (obj instanceof Date) {
        const copy = new Date();

        copy.setTime(obj.getTime());

        return copy;
    } else if (obj instanceof Array) {
        let copy = [];

        for (let i = 0, len = obj.length; i < len; i++) {
            copy[i] = clone(obj[i]);
        }

        return copy;
    } else if (obj instanceof Object) {
        let copy = {};

        for (let key in obj) {
            if (obj.hasOwnProperty(key)) { copy[key] = clone(obj[key]); }
        }

        return copy;
    } else {
        throw new Error(' ');
    }
};

export {clone};



